import {request} from '../../request.js'
export default {
	data() {
		return {
			flag: true,
			text: '',
			// 搜索内容
			searchText: '',
			list: []
		};
	},
	components:{
		// Search
	},
	onShow() {
		uni.getStorage({
			key: 'keywords',
			success: res => {
				this.$store.state.keywords = res.data
				this.list = res.data
			}
		})
	},
	methods:{
		//	监听搜索框输入的值
		handleSearch(event) {
			this.searchText = event.detail.value;
		},
		// 请求搜索数据
		searchData() {
			request({
				url: 'index.php?s=wap/goods/goodsSearchList',
				method: 'post',
				data: {
					sear_name: this.searchText
				}
			}).then(res => {
				if(res.data.code == 200) {
					// 提交到vuex做保存
					this.$store.commit('search',res.data);
					uni.navigateTo({
						url: '../goods-result/goods-result'
					})
				}
			})
		},
		// 点击搜索
		search() {
			if(this.searchText == '') {
				uni.showToast({
					title: '请输入要搜索的产品',
					icon: 'none'
				})
				return
			}
			else {
				// 储存搜索历史
				this.$store.commit('saveKeywords',{keywords: this.searchText});
				this.searchData();
				
			}
		},
		handleClick(item) {
			this.searchText = item;
			this.searchData();
		},
		
	}
}
